div[class*="font-item"] {
  padding: 1rem 0 1rem 0;
  border-bottom: 1px solid rgb(185, 185, 185);
}

/* name  */
.chakra-radio__label {
  font-size: 1rem !important;
}

/* author  */
.author {
  margin: 0.5rem 0 0.5rem 0;
  font-size: 0.8rem;
  color: rgb(161, 161, 161);
}

/* buttons  */

.buttons {
  text-align: right;
  font-size: 1rem;
}

.font-link {
  margin-right: 0.2rem;
}

input.fav-button {
  display: none;
}

.icon-heart {
  color: rgb(185, 185, 185);
  cursor: pointer;
}

input.fav-button:checked ~ .icon-heart {
  color: rgb(255, 64, 96);
}
